<template>
  <div class="hello">
      <input type="text" v-model="uid"><br>
      <input type="password" v-model="upw"><br>
      <button v-on:click="login">로그인</button>
      <button v-on:click="moveToJoin">회원가입</button>
  </div>
</template>

<script>
import VueCookies from 'vue-cookies'
import final from '../final'

export default {
  name: 'Login',
  data () {
    return {
      uid: '',
      upw: ''
    }
  },
  beforeCreate() {
    const token = VueCookies.get(final.cookie.accessToken)
    console.log('token : ' + token)
    if(token != undefined && token != '') {
      const user = VueCookies.get(final.cookie.user);
      this.$store.commit('setUser', user)
      this.$axios.defaults.headers.common['Authorization'] = 'bearer ' + token;
      this.$router.push("/home")
    }
  },
  methods: {
    login() {
      console.log('---- start login ----')
      var params = {
        uid: this.uid,
        upw: this.upw
      }

      var that = this;

      this.$axios.post("/auth/login", params)    
        .then(function(res){     
            console.log('res.data.status : ' + res.data.status)
            console.log('typeof : ' + typeof(res.data.status))
            switch(res.data.status) {
              case 1:
                that.$store.commit('setUser', res.data.user)
                const userJson = JSON.stringify(res.data.user)
                console.log('input userJson: ' + userJson)
                VueCookies.set(final.cookie.user, res.data.user)
                VueCookies.set(final.cookie.accessToken, res.data.token)
                that.$axios.defaults.headers.common['Authorization'] = 'bearer ' + res.data.token;
                that.$router.push("/home")
                break;
              case 0:
                alert('아이디 없음')
                break;
              case 2:
                alert('비번 틀림')
                break;
              case 3:
                alert('다중 로그인 시도 (다른 사람이 사용 중 입니다.)')
                break;
              default:
                alert('에러 발생')
                break;
            }
            
        })    
        .catch((errors) => {    
            console.log("jjjjjjCannot log in : " + errors)    
        })   
    },

    moveToJoin() {
      this.$router.push("/join")
    }
  }
}
</script>



